import React, { useState, ChangeEvent, FormEvent } from 'react';
import styled from '@emotion/styled';
import Input from '../components/Input';
import { Link, useHistory } from 'react-router-dom';
import Button from '../components/Button';

import Logo from '../assets/logo.svg';
import { api } from '../utils/api';
import auth from '../utils/auth';
import Modal from '../components/Modal';
import Checkbox from '../components/Checkbox';

declare global {
  interface Window {
    chordscore: any;
    webkit: any;
  }
}

const Login = () => {
  const history = useHistory();
  const [info, setInfo] = useState({ email: '', password: '' });
  const [rememberMe, setRememberMe] = useState(false);

  const [errorMessage, setErrorMessage] = useState('');
  const [isErrorModalOpen, setIsErrorModalOpen] = useState(false);

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const {
      data: { success, data, alert },
    } = await api.post('/login', { ...info });
    if (success) {
      const {
        data: {
          data: {
            user: { email },
            total_alarm_count,
          },
        },
      } = await api.get('/user', {
        headers: { Authorization: `Bearer ${data?.token?.accessToken}` },
      });
      auth.setEmail(email);
      auth.setNoticeCount(total_alarm_count);
      auth.setToken(data?.access_token?.access_token);
      auth.setRefreshToken(data?.access_token?.refresh_token);
      auth.setRememberMe(rememberMe);
      history.push('/');
      window.location.reload();
    } else {
      if (alert) {
        setErrorMessage(alert);
        setIsErrorModalOpen(true);
      }
    }
  };
  var varUA = navigator.userAgent;

  const kakao_login = () => {
    if (varUA.match('chordscore/Android') != null) {
      window.chordscore.postMessage(JSON.stringify({ type: 'kakaoLoginCheck' }));
    } else if (varUA.match('chordscore/iOS') != null) {

      window.webkit.messageHandlers.kakaoLoginCheckHandler.postMessage('_');
    } else {
      window.location.href = 'https://api.chordscore.com/api/kakao/login';
    }
  };
  return (
    <Container>
      <ContentsWrapper>
        <LogoWrap>
          <Logo />
        </LogoWrap>
        <Info>당신이 찾던 코드 악보들, 코드스코어에서 만나보세요.</Info>
        <Form onSubmit={handleSubmit}>
          <InputWrap>
            <Input
              name="email"
              placeholder="아이디(E-mail)를 입력해주세요."
              onChange={handleChange}
            />
            <Input
              name="password"
              type="password"
              placeholder="비밀번호를 입력해주세요."
              onChange={handleChange}
            />
            <Checkbox
              name="rememberMe"
              type="checkbox"
              checked={rememberMe}
              onChange={() => setRememberMe(!rememberMe)}>
              로그인 상태 유지
            </Checkbox>
          </InputWrap>
          <ButtonWrap>
            <Button styleType="blue">로그인</Button>
            <Button
              styleType="outline"
              onClick={() => history.push('/auth/register')}
            >
              회원가입
            </Button>
          </ButtonWrap>
        </Form>
        <TextButton to="/auth/find-password">
          비밀번호를 잊어버리셨나요?
        </TextButton>
        {<Button
          styleType="kakao"
          onClick={kakao_login}
        >
          Kakao로 로그인
        </Button>}
        {varUA.match('chordscore/iOS') != null && (

          <Button
            styleType="apple"
            onClick={() =>
              (window.location.href = `https://api.chordscore.com/apple/login`)
            }
          >
            Apple로 로그인
          </Button>)}
      </ContentsWrapper>
      <Modal
        open={isErrorModalOpen}
        onClose={() => setIsErrorModalOpen(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => setIsErrorModalOpen(false)}
        title="에러"
        content={errorMessage}
      />
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  min-height: 100vh;

  background-image: url(${require('../assets/login-light.jpg')});
  background-size: cover;

  html[color-mode='dark'] & {
    background-image: url(${require('../assets/login-dark.jpg')});
  }
`;

const ContentsWrapper = styled.div`
  display: flex;
  flex-direction: column;

  width: 100%;
  max-width: 400px;

  align-items: center;

  @media screen and (max-width: 500px) {
    width: 88%;
    max-width: unset;
  }
`;

const LogoWrap = styled.div`
  margin-bottom: 10px;
  & {
    svg {
      width: 300px;
      height: auto;
    }
  }

  html[color-mode='dark'] & path {
    fill: white;
  }

  @media screen and (max-width: 500px) {
    svg {
      width: 191px;
      height: 27px;
    }
  }
`;

const Info = styled.span`
  display: block;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: #3a3a3a;
  line-height: 1.86;
  margin-bottom: 70px;

  html[color-mode='dark'] & {
    color: white;
    font-weight: 300;
  }

  @media screen and (max-width: 500px) {
    margin-bottom: 25px;
  }
`;

const Form = styled.form`
  width: 100%;
`;

const InputWrap = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 40px;

  & > * {
    margin-bottom: 20px;
  }
`;

const ButtonWrap = styled.div`
  width: 100%;
  margin-bottom: 30px;

  & > *:first-of-type {
    margin-bottom: 10px;
  }

  @media screen and (max-width: 500px) {
    margin-bottom: 39px;
  }
`;

const TextButton = styled(Link)`
  font-size: 13px;
  line-height: 2;
  text-align: center;
  color: #1e1e1e;
  text-decoration: underline;
  margin-bottom: 91px;

  html[color-mode='dark'] & {
    color: white;
  }

  @media screen and (max-width: 500px) {
    margin-bottom: 30px;
  }
`;

export default Login;
